.header__action {
    margin: 0;
    padding: 0;
    background: #000000;
    color: inherit;
    font-weight: 400;
    cursor: pointer;
    border: 0;
}

.header__action:hover {
    opacity: 0.6;
}

@media (max-width: 950px) {
    .header__action {
        margin-right: 27px;
    }
}